import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"

const Nav = () => (
  <nav className="nav">
    <ul className="nav__items">
      <StaticQuery
        query={graphql`
                query {
                  allStrapiPages {
                    edges {
                      node {
                        id
                        title,
                        slug,
                        isHidden
                      }
                    }
                  }
                }
              `}
        render={data =>
          data.allStrapiPages.edges.map((page, i) => {
            return (
              <li key={page.node.id}>
                <Link to={`/${page.node.slug}`}>
                  {page.node.title}
                </Link>
              </li>
            )
          })
        }
      />
    </ul>
  </nav>
)

export default Nav  